
"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Textarea } from "@/components/ui/textarea";
import { ChevronLeft, ChevronRight, Settings, Trash2 } from "lucide-react";
import { useState } from "react";

interface RentalPrice {
  id: number;
  period: string;
  pricelist: string;
  price: number;
}

interface Product {
  id: string;
  name: string;
  rentalPrices: RentalPrice[];
  extraHour1: number;
  extraDays: number;
  extraHour2: number;
}

const sampleProducts: Product[] = [
    {
        id: 'PROD001',
        name: 'Standard Wheelchair',
        rentalPrices: [
            { id: 1, period: '1 Day', pricelist: 'Standard', price: 25 },
            { id: 2, period: '1 Week', pricelist: 'Standard', price: 150 },
            { id: 3, period: '1 Month', pricelist: 'Standard', price: 500 },
        ],
        extraHour1: 5,
        extraDays: 20,
        extraHour2: 5,
    },
    {
        id: 'PROD002',
        name: 'HD Projector',
        rentalPrices: [
            { id: 1, period: '1 Day', pricelist: 'Standard', price: 100 },
        ],
        extraHour1: 20,
        extraDays: 90,
        extraHour2: 20,
    }
];


export default function ProductPage() {
    const [products, setProducts] = useState<Product[]>(sampleProducts);
    const [currentProductIndex, setCurrentProductIndex] = useState(0);

    const currentProduct = products[currentProductIndex];

    const handleInputChange = (field: keyof Product, value: any) => {
        const updatedProducts = [...products];
        updatedProducts[currentProductIndex] = { ...currentProduct, [field]: value };
        setProducts(updatedProducts);
    };

    const handlePriceChange = (priceId: number, field: keyof RentalPrice, value: any) => {
        const updatedPrices = currentProduct.rentalPrices.map(p => 
            p.id === priceId ? { ...p, [field]: value } : p
        );
        handleInputChange('rentalPrices', updatedPrices);
    };
    
    const addPriceRow = () => {
        const newId = currentProduct.rentalPrices.length > 0 ? Math.max(...currentProduct.rentalPrices.map(p => p.id)) + 1 : 1;
        const newPrice: RentalPrice = { id: newId, period: '', pricelist: '', price: 0 };
        handleInputChange('rentalPrices', [...currentProduct.rentalPrices, newPrice]);
    };

    const removePriceRow = (priceId: number) => {
        const updatedPrices = currentProduct.rentalPrices.filter(p => p.id !== priceId);
        handleInputChange('rentalPrices', updatedPrices);
    };
    
    const createNewProduct = () => {
        const newProduct: Product = {
            id: `PROD${(products.length + 1).toString().padStart(3, '0')}`,
            name: 'New Product',
            rentalPrices: [{ id: 1, period: '1 Day', pricelist: 'Standard', price: 0 }],
            extraHour1: 0,
            extraDays: 0,
            extraHour2: 0,
        };
        setProducts([...products, newProduct]);
        setCurrentProductIndex(products.length);
    };

    const navigate = (direction: 'next' | 'prev') => {
        if (direction === 'next') {
            setCurrentProductIndex(prev => (prev + 1) % products.length);
        } else {
            setCurrentProductIndex(prev => (prev - 1 + products.length) % products.length);
        }
    };


  return (
    <div className="p-4 md:p-8 bg-gray-50">
      <div className="flex flex-col md:flex-row items-start md:items-center justify-between mb-6 gap-4">
        <div className="flex items-center gap-4">
          <Button onClick={createNewProduct}>Create</Button>
          <div className="flex items-center gap-2">
            <h1 className="text-xl md:text-2xl font-bold text-gray-800">
                <Input value={currentProduct.name} onChange={e => handleInputChange('name', e.target.value)} className="text-xl md:text-2xl font-bold !h-auto p-0 border-0 focus-visible:ring-0"/>
            </h1>
            <Settings className="h-5 w-5 text-gray-600" />
          </div>
        </div>
        <div className="flex items-center gap-2">
            <span className="text-sm font-medium">{currentProductIndex + 1}/{products.length}</span>
            <Button variant="outline" size="icon" className="h-8 w-8" onClick={() => navigate('prev')}>
                <ChevronLeft className="h-4 w-4" />
            </Button>
             <Button variant="outline" size="icon" className="h-8 w-8" onClick={() => navigate('next')}>
                <ChevronRight className="h-4 w-4" />
            </Button>
        </div>
      </div>

       <div className="mb-6">
        <Button variant="outline">Update stock</Button>
      </div>

      <Card>
        <CardContent className="p-4 md:p-6 grid grid-cols-1 md:grid-cols-2 gap-8">
          <div>
            <h2 className="text-lg font-semibold mb-4">General Product info</h2>
             <div className="space-y-4">
                <div>
                    <Label htmlFor="product-name">Product Name</Label>
                    <Input id="product-name" value={currentProduct.name} onChange={e => handleInputChange('name', e.target.value)} />
                </div>
                 <div>
                    <Label htmlFor="product-description">Description</Label>
                    <Textarea id="product-description" placeholder="Enter product description..."/>
                </div>
            </div>
          </div>
          <div>
            <h2 className="text-lg font-semibold mb-4">Rental Pricing</h2>
            <div className="mb-6">
                 <Table>
                    <TableHeader>
                        <TableRow>
                        <TableHead>Rental Period</TableHead>
                        <TableHead>Pricelist</TableHead>
                        <TableHead>Price</TableHead>
                        <TableHead><span className="sr-only">Actions</span></TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {currentProduct.rentalPrices.map((p) => (
                        <TableRow key={p.id}>
                            <TableCell>
                                <Input placeholder="e.g. 1 Day" value={p.period} onChange={e => handlePriceChange(p.id, 'period', e.target.value)}/>
                            </TableCell>
                            <TableCell>
                                 <Input placeholder="e.g. Standard" value={p.pricelist} onChange={e => handlePriceChange(p.id, 'pricelist', e.target.value)} />
                            </TableCell>
                            <TableCell>
                                <Input placeholder="e.g. 100" type="number" value={p.price} onChange={e => handlePriceChange(p.id, 'price', parseFloat(e.target.value) || 0)} />
                            </TableCell>
                            <TableCell>
                                <Button variant="ghost" size="icon" onClick={() => removePriceRow(p.id)}>
                                    <Trash2 className="h-4 w-4 text-destructive" />
                                </Button>
                            </TableCell>
                        </TableRow>
                        ))}
                    </TableBody>
                </Table>
                <Button variant="link" className="mt-2 px-0" onClick={addPriceRow}>+ Add a row</Button>
            </div>
            
            <h3 className="text-md font-semibold mb-4">Rental Reservations charges</h3>
            <div className="space-y-4">
                <div className="flex items-center justify-between">
                    <Label htmlFor="extra-hour-1">Extra Hour</Label>
                    <div className="flex items-center gap-2">
                        <Input id="extra-hour-1" className="w-32" type="number" placeholder="0.00" value={currentProduct.extraHour1} onChange={e => handleInputChange('extraHour1', parseFloat(e.target.value) || 0)} />
                        <span>Rs</span>
                    </div>
                </div>
                 <div className="flex items-center justify-between">
                    <Label htmlFor="extra-days">Extra Days</Label>
                     <div className="flex items-center gap-2">
                        <Input id="extra-days" className="w-32" type="number" placeholder="0.00" value={currentProduct.extraDays} onChange={e => handleInputChange('extraDays', parseFloat(e.target.value) || 0)} />
                        <span>Rs</span>
                    </div>
                </div>
                 <div className="flex items-center justify-between">
                    <Label htmlFor="extra-hour-2">Extra Hour</Label>
                     <div className="flex items-center gap-2">
                        <Input id="extra-hour-2" className="w-32" type="number" placeholder="0.00" value={currentProduct.extraHour2} onChange={e => handleInputChange('extraHour2', parseFloat(e.target.value) || 0)} />
                        <span>Rs</span>
                    </div>
                </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
